import React, { Component } from 'react';
import './Image.css'
import './ImageList.css'

class Image extends Component {
    constructor(props) {
        super(props);
        this.myRef = React.createRef();
        this.state = {
            spanCount: 1
        };
    }

    componentDidMount() {
        this.myRef.current.addEventListener('load', () =>
            console.log(this.myRef.current.clientHeight));
        const spanCountNew = Math.round(this.myRef.current.clientHeight / 5) + 1;
        this.setState({
            spanCount: spanCountNew
        });
    }

    render() {
        const { image } = this.props;
        return (
            <img className="img-container" style={{ gridRowEnd: `span ${this.state.spanCount}` }} ref={this.myRef} key={image.id} src={image.urls.small} alt="" />
        );
    }
}

export default Image;