import React, { Component } from 'react';
import './SearchBar.css'

class SearchBar extends Component {

    state =
        {
            search: ""
        };

    inputChanged = event => {
        this.setState({
            search: event.target.value
        });
    };

    searchImage= () =>
    {
        console.log("Basarili");
        this.props.onSearchImage(this.state.search);         
    }


    render() {
        return (
            <div className="search-bar-container ui icon input">
                <input type="text" onKeyPress={(e)=>{
                        if(e.key==="Enter"){
                            this.searchImage();
                        }
                    }
                } onChange={this.inputChanged.bind(this)} placeholder="Search..."/>
                    <button className="inverted-circular search" onClick={this.searchImage}>
                        <i className="inverted circular search link icon"></i>
                    </button>
            </div>
            )
        }
                
}
export default SearchBar;